module.exports = "<!--\r\n绑定 HTML Class：\r\n1. 对象语法\r\n<div class=\"static\"\r\n     v-bind:class=\"{ active: isActive, 'text-danger': hasError }\">\r\n</div>\r\n\r\n\r\n2. 绑定内联样式\r\n\r\n2.1 对象语法\r\n\r\n<div v-bind:style=\"{ color: activeColor, fontSize: fontSize + 'px' }\"></div>\r\n\r\ndata: {\r\n  activeColor: 'red',\r\n  fontSize: 30\r\n}\r\n\r\n2.2 数组语法\r\n<div v-bind:style=\"[baseStyles, overridingStyles]\"></div>\r\n\r\n2.3 自动添加前缀\r\n\r\n当 v-bind:style 使用需要添加浏览器引擎前缀的 CSS 属性时，如 transform，Vue.js 会自动侦测并添加相应的前缀。\r\n\r\n2.4  多重值\r\n<div :style=\"{ display: ['-webkit-box', '-ms-flexbox', 'flex'] }\"></div>\r\n这样写只会渲染数组中最后一个被浏览器支持的值。在本例中，如果浏览器支持不带浏览器前缀的 flexbox，那么就只会渲染 display: flex。\r\n-->\r\n\r\n<template>\r\n    <div>\r\n        <div class=\"case-item\">\r\n            <h4>1. 对象语法</h4>\r\n            <div class=\"show-div\" v-bind:class=\"classObj\"></div>\r\n            <el-checkbox-group v-model=\"checkedList\">\r\n                <el-checkbox v-for=\"className in Object.keys(classObj)\" :label=\"className\"\r\n                             :key=\"className\"></el-checkbox>\r\n            </el-checkbox-group>\r\n            <h4>2. 数组语法</h4>\r\n            <div class=\"show-div\" v-bind:class=\"checkedList\"></div>\r\n            <el-checkbox-group v-model=\"checkedList\">\r\n                <el-checkbox v-for=\"className in Object.keys(classObj)\" :label=\"className\"\r\n                             :key=\"className\"></el-checkbox>\r\n            </el-checkbox-group>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script lang=\"ts\">\r\n    import {Vue, Component, Watch} from 'vue-property-decorator'\r\n\r\n    @Component\r\n    export default class BindingHTMLClasses extends Vue {\r\n\r\n        public checkList = []\r\n        public classObj = {\r\n            borderBold: false,\r\n            backgroundBlue: false\r\n        }\r\n\r\n        get checkedList() {\r\n            return this.checkList;\r\n        }\r\n\r\n        set checkedList(newChecked) {\r\n            this.checkList = newChecked\r\n            Object.keys(this.classObj).map(item => {\r\n                this.classObj[item] = false\r\n            })\r\n            newChecked.map(item => {\r\n                this.classObj[item] = true\r\n            })\r\n        }\r\n    }\r\n</script>\r\n\r\n<style rel=\"stylesheet/scss\" lang=\"scss\" scoped>\r\n    .borderBold {\r\n        border: 5px solid black;\r\n    }\r\n\r\n    .backgroundBlue {\r\n        background-color: blue;\r\n    }\r\n\r\n    .show-div {\r\n        width: 50px;\r\n        height: 50px;\r\n    }\r\n</style>\r\n\r\n\r\n";